  .body--catalog main {
    padding-top: 114px;
  }
}
.body--catalog .basket-right {
  top: 114px;
  padding-top: 36px;
}
.body--catalog .position-list__item:nth-child(n+5) {
  display: flex;
}
.body--basket h1 {
  display: none;
  position: absolute;
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
}
@media (min-width: 768px) {
  .body--basket h1 {
    display: block;
    font-size: 12px;
    line-height: 15px;
    margin-left: 40px;
  }
}
@media (min-width: 1440px) {
