    margin-left: 110px;
  }
}
.half-site--left {
  width: 100%;
}
@media (min-width: 1440px) {
  .half-site--left {
    width: calc(100% - 322px - 30px);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.catalog-button {
  display: none;
  width: 146px;
}
@media (min-width: 768px) {
  .catalog-button {
    display: block;
  }
}
@media (min-width: 1440px) {
  .catalog-button {
    display: block;
    padding-top: 31px;
    padding-bottom: 36px;
    margin-right: 25px;
  }
}
.catalog-button__button {
  transition: border 0.2s;
  width: 100%;
  height: 46px;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 20px;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.catalog-button__button:hover {
  transition: border 0.2s;
  border: 2px solid #fec60a;
}
.catalog-button__button:hover .catalog-button__icon {
  transition: fill 0.2s;
  fill: #fec60a;
}
.catalog-button__button:hover .catalog-button__text {
  transition: color 0.2s;
  color: #fec60a;
}
.catalog-button__icon {
  transition: fill 0.2s;
  fill: white;
}
.catalog-button__text {
  transition: color 0.2s;
  margin: 0;
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 17px;
  line-height: 21px;
  color: white;
  margin-left: 9px;
}
.basket-right {
  display: none;
  z-index: 2;
  position: sticky;
  top: 0;
  width: 282px;
  height: calc(100vh - 150px - 20px);
  min-height: 600px;
  padding-top: 150px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 15px;
  margin-right: 30px;
  background-color: #FFFFFF;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px 1px 2px 2px rgba(0, 0, 0, 0.25);
}
@media (min-width: 1440px) {
  .basket-right {
    display: block;
  }
}
.basket-right__wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.basket-right__arrow {
  border: none;
  position: absolute;
  top: 45px;
  left: -38px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 24px;
  background: #58FF91;
  border-radius: 13px 0px 0px 13px;
}
.basket-right__arrow__svg {
  height: 11px;
  width: 17px;
}
.basket-right__arrow__path {
  fill: white;
}
.basket-right__info {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.basket-position-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.basket-right__message-empty-basket {
  margin: 0;
  width: fit-content;
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 16px;
  color: #9F9F9F;
}
.pluses-list {
  list-style: none;
  margin: 0;
  margin-top: auto;
  margin-bottom: auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.pluses-list__item {
