}
.pluses-list__item__description {
  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 19px;
  color: #959595;
  margin-top: 5px;
  width: 142px;
}
.basket-right__button {
  width: 216px;
  height: 46px;
  margin-top: auto;
  border: 0;
  background: #E2E2E2;
  box-shadow: 0px 2px 0px rgba(255, 214, 0, 0.93);
  border-radius: 20px;
