.searcher {
  position: relative;
  height: 35px;
  display: flex;
  flex-direction: row;
  margin-right: 27.5px;
  margin-left: auto;
}
@media (min-width: 768px) {
  .searcher {
    flex-grow: 1;
    height: 48px;
    max-width: 600px;
    margin-left: 26px;
    margin-right: 30px;
  }
}
@media (min-width: 1440px) {
  .searcher {
    margin-left: 20px;
    margin-right: 80px;
    padding-top: 31px;
    padding-bottom: 41px;
  }
}
.searcher__searcher {
  border: none;
  background-color: transparent;
  padding: 0;
}
@media (min-width: 768px) {
  .searcher__searcher {
    position: absolute;
    top: 10px;
    left: 10px;
  }
}
@media (min-width: 1440px) {
  .searcher__searcher {
    top: calc(10px + 31px);
    left: 14px;
  }
}
.searcher__searcher-img {
  width: 20px;
  height: 30px;
}
@media (min-width: 768px) {
  .searcher__searcher-img {
    position: absolute;
    width: 19px;
    height: 30px;
    top: 0;
    left: 0;
  }
}
.searcher__searcher-img--path {
  fill: white;
}
.searcher__searcher-img--line {
  stroke: white;
}
@media (min-width: 768px) {
  .searcher__searcher-img--mobile {
    display: none;
  }
}
.searcher__searcher-img--tablet {
  display: none;
}
@media (min-width: 768px) {
  .searcher__searcher-img--tablet {
    display: block;
  }
}
.searcher__input {
  display: none;
  outline: none;
  width: 100%;
  font-family: "MA Medium", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  background-color: transparent;
  color: white;
  box-sizing: border-box;
  border-radius: 10px;
}
.searcher__input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.searcher__input:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.searcher__input::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.searcher__input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}
@media (min-width: 768px) {
  .searcher__input {
    display: block;
    border: 3px solid white;
    font-size: 20px;
    line-height: 24px;
    padding-left: 48px;
    box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.25);
  }
  .searcher__input:hover {
    border: 3px solid white;
  }
  .searcher__input:focus {
    border: 3px solid white;
  }
  .searcher__input:active {
    border: 3px solid white;
  }
}
