.user-menu {
  width: fit-content;
}
.user-menu__list {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}
.item-menu {
  position: relative;
  height: auto;
  padding-left: 13.5px;
  padding-right: 13.5px;
  padding-top: 14px;
  padding-bottom: 15px;
}
.item-menu::before {
  content: "";
  transition: width 0.2s;
  position: absolute;
  bottom: 0;
  left: 50%;
  height: 5px;
  width: 0;
  transform: translateX(-50%);
  background-color: #FEC60A;
}
.item-menu:hover .link-menu__img--path {
  fill: #FEC60A;
  transition: color 0.2s;
}
.item-menu:hover .link-menu {
  color: #FEC60A;
  transition: color 0.2s;
}
.item-menu:hover::before {
  width: 100%;
  transition: width 0.2s;
}
@media (min-width: 768px) {
  .item-menu {
    padding-top: 29px;
    padding-bottom: 41px;
  }
}
@media (min-width: 1440px) {
  .item-menu {
    padding-top: 37px;
    padding-bottom: 45px;
  }
}
.item-menu .link-menu {
  display: flex;
  flex-direction: сolumn;
