.basket-info {
  width: calc(100% - 20px - 20px);
  height: 73vw;

  margin-left: 20px;
  margin-right: 20px;
  padding-top: 25px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;

  box-shadow: 0px 1px 1px 2px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  background-color: white;

  @media (min-width: 768px) {
    width: calc(25vw - 12px - 12px);
    max-width: 350px;
    height: calc(25vw - 15px - 9px);

    padding-top: 15px;
    padding-right: 12px;
    padding-bottom: 9px;
    padding-left: 12px;
    margin-left: auto;
    margin-right: @padding_on_768;
  }

  @media (min-width: 1440px) {
    width: calc(25% - 27px - 27px);
    height: calc(350px - 33px - 10px);
    
    padding-top: 33px;
    padding-bottom: 10px;
    padding-left: 27px;
    padding-right: 27px;
    margin-right: @padding_on_1440;
  }
}

.basket-info__list {
  list-style: none;
  margin: 0;
  padding: 0;

  width: 100%;
  height: 40%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.basket-info__item {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  height: 25%;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;

  @media (min-width: 768px) {
    font-size: .95vw;
    line-height: .95vw;

    height: 25%;
  }

  @media (min-width: 1440px) {
    font-size: 14px;
    line-height: 17px;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 0;

    width: 100%;
    height: 0.5px;

    background-color: #C4C4C4;
  }

  &:first-child {
    padding-top: 0;
  }

  &:last-child {
    padding-bottom: 0;
    &::before {
      display: none;
    }
  }

  &--promo .basket-info__item__value {
    color: #FF0000;
  }

  &--delivery .basket-info__item__value {
    color: #FF0000;
  }

  &--bonus .basket-info__item__info {
    color: #FEC60A;
  }

  &--bonus .basket-info__item__value {
    color: #7DDB6E;
  }
}

.basket-info__summ {
  margin: 0;
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 27px;
  line-height: 33px;

  @media (min-width: 768px) {
    font-size: 2.5vw;
    line-height: 2.5vw;
  }

  @media (min-width: 1440px) {
    font-size: 24px;
    line-height: 29px;
  }
}

.basket-info__button-buy {
  border: 0;

  width: 100%;
  height: 26%;

  border-radius: 20px;
  background: #00C036;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  color: #FFFFFF;

  @media (min-width: 768px) {
    height: 25%;

    font-size: 10px;
    line-height: 12px;
  }

  @media (min-width: 1440px) {
    height: 92px;

    font-size: 21px;
    line-height: 26px;
  }
}
