.basket-list {
  flex-grow: 1;
  margin-left: @padding_on_320;
  margin-right: @padding_on_320;

  @media (min-width: 768px) {
    margin: 0;
    max-width: calc(65% - @padding_on_768);

    margin-top: 39px;
    margin-left: @padding_on_768;
  }

  @media (min-width: 1440px) {
    max-width: calc(65% - @padding_on_1440);

    margin-top: 67px;
    margin-left: @padding_on_1440;
  }
}

.basket-options {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
  }
}

.basket-options__item {
  position: relative;

  margin-top: 7.5px;
  margin-bottom: 7.5px;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    margin: 0;
  }
}

.basket-options__item--adress {
  @media (min-width: 768px) {
    flex-grow: 1;
    margin-right: 30px;

    & .basket-options__item__select {
      width: 100%;
    }
  }
}

.basket-options__item__select {
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;

  width: 100%;
  height: calc(55px - 9px - 9px);

  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 10px;
  padding-right: 10px;
  

  box-sizing: border-box;
  border-radius: 10px;
  border: 1px solid #6A6A6A;

  &:focus-visible {
    outline: 0;
  }

  @media (min-width: 768px) {
    margin: 0;

    width: auto;
    height: calc(40px - 7px - 7px);
    border: 1px solid #6A6A6A;

    padding-top: 7px;
    padding-bottom: 7px;
    padding-right: 10px;
    padding-left: 10px;
  }

  @media (min-width: 1440px) {
    height: calc(60px - 10px - 10px);

    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 20px;
    padding-left: 20px;

    border: 2px solid #6A6A6A;
  }
}


.basket-options__item__select,
.basket-options__item__option {
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  color: #393939;

  @media (min-width: 768px) {
    font-size: 9px;
    line-height: 11px;
  }

  @media (min-width: 1440px) {
    font-size: 13px;
    line-height: 16px;
  }
}

.basket-options__item__svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;

  @media (min-width: 768px) {
    right: 10px;

    width: 12px;
    height: 6px;
  }

  @media (min-width: 1440px) {
    right: 20px;

    width: 20px;
    height: 12px;
  }
}

.basket-list__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: 20px;

  display: flex;
  flex-direction: column;
}

.basket-list__item {
  width: 100%;
  position: relative;

  display: flex;
  flex-direction: column;

  @media(min-width: 768px) {
    width: auto;
  }

  @media(min-width: 1440px) {
    min-width: 712px;
  }

  &--open .basket-position {
    &::before {
      z-index: -1;
      content: "";

      position: absolute;
      left: 10px;
      bottom: -44px;

      width: 1px;
      height: 44px;

      background-color: #C4C4C4;
    }

    &::after {
      z-index: -1;
      content: "";
  
      position: absolute;
      left: 10px;
      bottom: -44px;
  
      width: 44px;
      height: 1px;
  
      background-color: #C4C4C4;
    }
  }
}

.basket-position {
  position: relative;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;

  background-color: white;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.25);
  border-radius: 16px;

  @media (min-width: 768px) {
    flex-wrap: nowrap;

    padding-left: 10px;
    padding-right: 10px;
    padding-top: 7px;
    padding-bottom: 7px;
  }

  @media (min-width: 1440px) {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.basket-list__item__icon-wrapper {
  width: 20%;
  height: 20%;

  margin-top: 10px;
  margin-left: 10px;

  display: flex;
  justify-content: center;
  align-items: center;

  @media(min-width: 768px) {
    margin: 0;

    width: 70px;
    height: 70px;
  }
}

.basket-list__item__icon {
  width: auto;
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

.basket-list__item__name {
  display: block;
  width: calc(80% - 10px - 20px - 30px);
  flex-grow: 1;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 15px;
  color: black;

  margin-left: 20px;
  margin-right: 30px;

  @media (min-width: 768px) {
    width: auto;
    max-width: 300px;

    font-size: 11px;
    line-height: 13px;

    margin-right: 5px;
    margin-left: 7px;
    margin-bottom: 11px;
  }

  @media (min-width: 1440px) {
    font-size: 13px;
    line-height: 16px;

    margin: 0;
    margin-bottom: 13px;
    margin-left: 10px;
  }
}

.basket-list__price {
  display: flex;
  flex-direction: column;

  margin-left: auto;
  margin-right: auto;

    &--promo {

    & .basket-list__price__value--before {
      position: relative;
      order: 1;

      width: fit-content;
      
      &::before {
        content: "";
        width: 100%;
        height: 0.1em;
        background: red;
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotate(-10deg);
      }
    }

    & .basket-list__price__value--now{
      order: 0;
      color: #FF0000;
    }
  }

  @media (min-width: 768px) {
    margin-left: auto;
    margin-right: auto;
  }
}

.basket-list__price__value {
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 24px;
  line-height: 26px;
  color: black;

  @media (min-width: 768px) {
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: 1440px) {
    font-size: 22px;
    line-height: 27px;
  }

  &--before {
    order: 1;
    font-size: 17px;
    line-height: 17px;

    @media (min-width: 768px) {
      font-size: 13px;
      line-height: 16px;
    }
  
    @media (min-width: 1440px) {
      font-size: 18px;
      line-height: 22px;
    }
  }
}

.basket-list__delete {
  position: absolute;
  top: 5px;
  right: 0;
  
  background-color: transparent;
  border: 0;

  &__svg {
    @media (min-width: 768px) {
      height: 25px;
      width: 22px;
    }
  
    @media (min-width: 1440px) {
      height: 40px;
      width: 30px;
    }
  }

  &__path {
    fill: #C4C4C4;
  }

  @media (min-width: 768px) {
    position: relative;
  }
}

.basket-mount {
  order: 2;
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-right: 25px;
  margin-left: auto;
  margin-bottom: calc(11px + 17px);

  @media (min-width: 768px) {
    margin: 0;

    margin-left: auto;
    margin-right: 15px;
    order: 0;
  }
  @media (min-width: 1440px) {
    margin: 0;
  }
}

.basket-mount__button {
  position: relative;
  border: 0;

  height: 25px;
  width: 25px;

  background-color: #0DB14B;

  &--minus {
    border-radius: 8px 0px 0px 8px;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);

      width: 9px;
      height: 2px;

      background-color: white;
    }
  }

  &--plus {
    border-radius: 0px 8px 8px 0px;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);

      width: 9px;
      height: 2px;

      background-color: white;
    }

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);

      width: 2px;
      height: 9px;

      background-color: white;
    }
  }
}

.basket-mount__value {
  width: 20px;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
}

.basket-list__button-open-buy-with-it {
  border: 0;
  margin: 0;
  padding: 0;
  background-color: transparent;

  position: absolute;
  right: 10px;
  bottom: 10px;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 13px;
  color: #52BA00;

  @media (min-width: 768px) {
    font-size: 9px;
    line-height: 11px;

    bottom: 15px;
    left:  90px;
  }

  @media (min-width: 1440px) {
    font-size: 11px;
    line-height: 13px;

    bottom: 15px;
    left: 100px;
  }

  &__svg {
    @media (min-width: 768px) {
      width: 8px;
      height: 5px;
    }
  
    @media (min-width: 1440px) {
      font-size: 11px;
      line-height: 13px;
  
      bottom: 15px;
      left: 100px;
    }
  }
}
