.prodcut-in-day {
  position: relative;

  background: #7ddb6e;

  width: 100%;

  padding-top: 42px;
  padding-bottom: 20px;

  @media (min-width: 768px) {
    width: calc(100% - @padding_on_768 - @padding_on_768);

    padding-left: @padding_on_768;
    padding-right: @padding_on_768;
    padding-top: 46px;
    padding-bottom: 42px;
  }

  @media (min-width: 1440px) {
    width: calc(100% - @padding_on_1440 - @padding_on_1440);

    padding-left: @padding_on_1440;
    padding-right: @padding_on_1440;
  }
}

.prodcut-in-day__wrapper {
  max-width: 1440px;

  margin-left: auto;
  margin-right: auto;
}

.prodcut-in-day__place-h2 {
  z-index: 1;
  position: absolute;
  top: -25px;
  left: 50%;
  transform: translateX(-50%);

  width: 199px;
  height: 54px;

  background: #ff0000;
  border-radius: 20px;

  @media (min-width: 768px) {
    left: auto;
    transform: translateX(0);
  }
}

.prodcut-in-day__h2 {
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);

  margin: 0;
  width: fit-content;

  font-family: "MA Bold", "Arial Narrow", sans-serif;
  text-align: center;
  vertical-align: middle;
  color: #ffffff;
}

.prodcut-in-day__list {
  max-height: 175px;
  width: calc(100% - 20px - 20px);
  position: relative;
  list-style: none;
  overflow: hidden;

  margin: 0;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;

  display: grid;
  justify-items: center;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  grid-template-rows: auto;
  grid-gap: 10px;

  @media (min-width: 768px) {
    max-height: 200px;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  }
  
  @media (min-width: 1440px) {
    padding: 10px; //Для тени из за оверфлоу
    height: 200px;
    grid-template-columns: repeat(auto-fill, minmax(175px, 1fr));
    gap: 40px;
  }
}

.product-in-day__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  background: #ffffff;
  border-radius: 20px;
  width: calc(100% - 9px - 10px);

  padding-top: 15px;
  padding-left: 9px;
  padding-right: 10px;
  padding-bottom: 14px;

  transition: box-shadow 0.08s;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.57);

  &:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.57);
  }

  @media (min-width: 768px) {
    padding-top: 18.06px;
    padding-left: 12.9px;
    padding-right: 12.9px;
    padding-bottom: 11.18px;

    height: calc(175px - 18.06px - 11.18px);
    width: calc(100% - 12.9px - 12.9px);
  }

  @media (min-width: 1440px) {
    padding-top: 17px;
    padding-left: 18px;
    padding-right: 18px;
    padding-bottom: 16px;

    height: calc(200px - 17px - 16px);
    width: calc(100% - 18px - 18px);
  }
}

