.position__name {
  margin: 0;

  font-family: "MA Bold","Arial Narrow",sans-serif;
  font-size: 8px;
  line-height: 11px;
  text-align: center;
  color: #000;

  margin-top: 15px;
  margin-bottom: 5px;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  @media (min-width: 768px) {
    margin-top: 8px;
    margin-bottom: 0;
    
    -webkit-line-clamp: 2;
  }
}

.position__price-place {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 768px) {
    margin-top: 4px;
  }
}

.price-place__price {
  font-family: "MA Bold", "Arial Narrow", sans-serif;
  text-align: center;

  margin-top: auto;
  margin-left: auto;
  margin-right: auto;

  color: #FF0000;
  font-size: 20px;
  line-height: 24px;

  @media (min-width: 1440px) {
    font-size: 25px;
    line-height: 30px;
  }
}

.price-place__add {
  @media (min-width: 768px) {
    width: 28.38px;
    height: 28.38px;
  }
}

.price-place__add__path {
  fill: #ff0000;
}

.prodcut-in-day__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  background-color: transparent;
  padding: 0;
  border: 0;

  @media (min-width: 768px) {
    display: none;
  }
}

.product-in-day__button--before {
  left: 3px;
}

.product-in-day__button--after {
  right: 3px;
}
