.buy-with-it {
  width: calc(100% - 30px);

  margin-left: 30px;
  margin-top: 15px;
  margin-bottom: 20px;

  @media (min-width: 768px) {
    width: 77%;
  }
}

.basket-list__item--open .buy-with-it__list{
  display: block;
}

.buy-with-it__list {
  display: none;
  padding: 0;
  margin: 0;
  list-style: none;
}

.buy-with-it__item {
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  margin-top: 20px;
  margin-bottom: 20px;

  background: #FFFFFF;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.25);
  border-radius: 16px;

  &::before {
    z-index: -1;
    content: "";

    position: absolute;
    bottom: -50px;

    left: 50%;
    transform: translateX(-50%);

    height: 50px;
    width: 1px;

    background-color: #C4C4C4;
  }

  
  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;

    &::before {
      display: none;
    }
  }

  @media (min-width: 768px) {
    flex-wrap: nowrap;

    padding-left: 20px;
    padding-right: 20px;
  }
}

.buy-with-it__item__icon-wrapper {
  width: 20%;
  height: 20%;

  margin-top: 10px;
  margin-left: 10px;

  display: flex;
  justify-content: center;
  align-items: center;

  @media(min-width: 768px) {
    margin: 0;

    width: 70px;
    height: 70px;
  }
}

.buy-with-it__item__icon {
  width: auto;
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

.buy-with-it__item__name {
  display: block;
  width: calc(80% - 10px - 10px - 10px);
  flex-grow: 1;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 16px;
  color: black;

  margin-left: 10px;
  margin-right: 10px;

  @media (min-width: 768px) {
    width: auto;
    max-width: 300px;

    font-size: 11px;
    line-height: 13px;

    margin-right: 5px;
    margin-left: 7px;
    margin-bottom: 11px;
  }

  @media (min-width: 1440px) {
    font-size: 13px;
    line-height: 16px;

    margin: 0;
    margin-bottom: 13px;
    margin-left: 10px;
  }
}

.buy-with-it__price {
  display: flex;
  flex-direction: column;

  margin-left: auto;
  margin-right: auto;

  @media (min-width: 768px) {
    margin-right: 15px;
  }

  @media (min-width: 1440px) {
    margin: 0;
  }

  &--promo {

    & .buy-with-it__price__value--before {
      position: relative;
      order: 1;
      width: fit-content;

      &::before {
        content: "";
        width: 100%;
        height: 0.1em;
        background: red;
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotate(-10deg);
      }
    }

    & .buy-with-it__price__value--now{
      order: 0;
      color: #FF0000;
    }
  }
}

.buy-with-it__price__value {
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 24px;
  line-height: 26px;
  color: black;

  @media (min-width: 768px) {
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: 1440px) {
    font-size: 22px;
    line-height: 27px;
  }

  &--before {
    order: 1;
    font-size: 17px;
    line-height: 17px;

    @media (min-width: 768px) {
      font-size: 13px;
      line-height: 16px;
    }
  
    @media (min-width: 1440px) {
      font-size: 18px;
      line-height: 22px;
    }
  }
}

.buy-with-it__button-add {
  background-color: white;
  border-radius: 50%;
  border: 0;
  padding: 0;

  width: 39.2px;
  height: 39.2px;

  margin-right: 10px;
  margin-bottom: 10px;

  &__svg {
    height: 40px;
    width: 40px;
  }

  &__path {
    fill: #13D05B;
  }

  @media (min-width: 768px) {
    margin: 0;
  }
}
