.catalog-button {
  display: none;
  width: 146px;

  @media (min-width: 768px) {
    display: block;
  }

  @media (min-width: 1440px) {
    display: block;
    padding-top: 31px;
    padding-bottom: 36px;
    margin-right: 25px;
  }
}

.catalog-button__button {
  transition: border .2s;
  width: 100%;
  height: 46px;

  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 20px;

  background-color: transparent;

  display: flex;
  flex-direction: row;
  align-items: center;

  
  &:hover {
    transition: border .2s;
    border: 2px solid #fec60a;

    & .catalog-button__icon {
      transition: fill .2s;
      fill: #fec60a;
    }

    & .catalog-button__text {
      transition: color .2s;
      color: #fec60a;
    }
  }
}

.catalog-button__icon {
  transition: fill .2s;
  fill: white;
}

.catalog-button__text {
  transition: color .2s;
  margin: 0;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 17px;
  line-height: 21px;
  color: white;

  margin-left: 9px;
}
