header .logo__img {
  padding-top: 21px;
  padding-bottom: 15px;
  margin-left: 5px;

  &:hover .logo__img--g {
    transition: fill .2s;
    fill: #fec60a;
  }
}

.logo {
  @media (min-width: 768px) {
    margin-left: auto;
  }
}

.logo__img {
  display: none;

  @media (min-width: 1440px) {
    display: block;
    
    width: 200px;
    height: 84px;
  }
}

.logo__img--g {
  transition: fill .2s;
  fill: white;
}
