.basket-promo {
  position: relative;
  width: 100%;

  margin-top: 20px;
  padding-top: 10px;
  padding-bottom: 20px;

  @media (min-width: 768px) {
    margin: 0;
    padding: 0;
  }
}

.basket-promo__wrapper {
  display: flex;
  flex-direction: column;

  width: 100%;

  margin-left: auto;
  margin-right: auto;

  @media (min-width: 768px) {
    flex-direction: row;
  }

  @media (min-width: 1440px) {
    max-width: 1440px;
  }
}

.basket-promo__item {
  margin-bottom: 100px;

  @media (min-width: 768px) {
    margin: 0;
  }
}

.basket-promo__list {
  max-height: 175px;
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden;

  width: calc(100% - 20px - 20px);

  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 20px;
  margin-right: 20px;
  margin-left: 20px;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: 10px;

  @media (min-width: 768px) {
    max-height: none;
    margin: 0;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 30px;

    padding-top: 25px;
    padding-bottom: 25px;
    padding-right: 25px;
  }

  @media (min-width: 1440px) {
    max-width: 1344px;
    
    grid-template-columns: repeat(auto-fill, minmax(175px, 1fr));
    gap: 30px;
  }
}

.basket-promo__name-wrapper {
  position: relative;


  @media (min-width: 768px) {
    margin-left: auto;
    width: 50px;
  }
}

.basket-promo__background {
  z-index: -1;
  position: absolute;

  height: 100%;
  width: 100vw;
  background-color: #FF3E3E;

  @media (min-width: 768px) {
    width: 100vw;
  }

  @media (min-width: 1440px) {
    width: 99vw;
  }
}

.basket-promo__name {
  margin: 0;

  text-align: center;
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;

  & span {
    color: #FFE769;
  }

  @media (min-width: 768px) {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translateX(-50%) translateY(-50%) rotate(-90deg);
    white-space: nowrap;

    font-size: 21px;
    line-height: 26px;
  }
}

.basket-promo__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  background-color: transparent;
  padding: 0;
  border: 0;

  @media (min-width: 768px) {
    display: none;
  }
}

.basket-promo__button--before {
  left: 3px;
}

.basket-promo__button--after {
  right: 3px;
}
