.user-menu {
  width: fit-content;
}

.user-menu__list {
  list-style: none;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin: 0;
  padding: 0;
}

.item-menu {
  position: relative;
  height: auto;

  padding-left: 13.5px;
  padding-right: 13.5px;
  padding-top: 14px;
  padding-bottom: 15px;

  &::before {
    content: "";
    transition: width .2s;
    
    position: absolute;
    bottom: 0;
    left: 50%;

    height: 5px;
    width: 0;

    transform: translateX(-50%);
    background-color: #FEC60A;
  }

  &:hover {
    & .link-menu__img--path {
      fill: #FEC60A;
      transition: color .2s;
    }

    & .link-menu {
      color: #FEC60A;
      transition: color .2s;
    }

    &::before {
      width: 100%;
      transition: width .2s;
    }
  }


  @media (min-width: 768px) {
    padding-top: 29px;
    padding-bottom: 41px;
  }

  @media (min-width: 1440px) {
    padding-top: 37px;
    padding-bottom: 45px;
  }
}

.item-menu .link-menu {
  display: flex;
  flex-direction: сolumn;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 1440px) {
    flex-direction: row;
  }
}

.link-menu__span {
  display: none;

  @media (min-width: 768px) {
    display: block;
  }

  @media (min-width: 1440px) {
    margin-left: 5px;
  }
}

.link-menu {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;

  font-family: "MA Bold", "Arial", sans-serif;
  font-size: 11px;
  line-height: 13px;

  height: 100%;

  color: white;
  text-decoration: none;


  @media (min-width: 1440px) {
    font-size: 17px;
    line-height: 21px;
  }
}

.link-menu__img {
  height: 26px;
  width: 30.55px;

  @media (min-width: 768px) {
    height: 33px;
    width: 36px;
    padding-bottom: 5px;
  }
}

.link-menu__img--path {
  fill: @user_elements_color;
}
